"use server";

import { createClient } from "@/lib/supabase/server";
import { InventoryItem } from "@/types/inventory";
import { revalidatePath } from "next/cache";

export async function getInventory() {
  const supabase = await createClient();
  
  const { data: { user } } = await supabase.auth.getUser();
  if (!user) throw new Error("Unauthorized");

  const { data: userRole } = await supabase
    .from("user_roles")
    .select("store_id")
    .eq("user_id", user.id)
    .single();

  if (!userRole?.store_id) throw new Error("Store not found");

  const { data, error } = await supabase
    .from("inventory")
    .select(`
      *,
      item:items (
        id,
        name,
        code,
        type,
        base_unit,
        safety_stock,
        latest_purchase_price,
        category:categories (
          name
        )
      )
    `)
    .eq("store_id", userRole.store_id)
    .order("variance", { ascending: true });

  if (error) {
    console.error("Error fetching inventory:", error);
    throw new Error("Failed to fetch inventory");
  }
  
  return data as unknown as InventoryItem[];
}

export async function getSuppliers() {
  const supabase = await createClient();
  const { data: { user } } = await supabase.auth.getUser();
  if (!user) return [];

  const { data: userRole } = await supabase
    .from("user_roles")
    .select("store_id")
    .eq("user_id", user.id)
    .single();

  if (!userRole?.store_id) return [];

  const { data } = await supabase
    .from("suppliers")
    .select("*")
    .eq("store_id", userRole.store_id)
    .eq("is_active", true)
    .order("name");

  return data || [];
}

interface InboundItem {
  itemId: string;
  quantity: number;
  unitPrice: number;
  expiryDate?: string;
}

export async function processInbound(supplierId: string | null, items: InboundItem[]) {
  const supabase = await createClient();
  const { data: { user } } = await supabase.auth.getUser();
  if (!user) return { error: "Unauthorized" };

  const { data: userRole } = await supabase
    .from("user_roles")
    .select("store_id")
    .eq("user_id", user.id)
    .single();

  if (!userRole?.store_id) return { error: "Store not found" };
  const storeId = userRole.store_id;

  // 1. Create Purchase Order (Draft -> Received)
  // For Direct Inbound, we assume it's received immediately.
  const { data: po, error: poError } = await supabase
    .from("purchase_orders")
    .insert({
      store_id: storeId,
      supplier_id: supplierId,
      status: "received", // Direct receive
      order_date: new Date().toISOString(),
      received_date: new Date().toISOString(),
      created_by: user.id,
      total_amount: items.reduce((sum, item) => sum + (item.quantity * item.unitPrice), 0)
    })
    .select()
    .single();

  if (poError) return { error: `Failed to create PO: ${poError.message}` };

  // 2. Insert Items
  // Note: The DB trigger 'trg_purchase_receipt_inventory' listens to UPDATE of received_quantity.
  // To trigger it, we might need to Insert 0 then Update? 
  // OR we can manually update inventory here.
  // Given we control the Action, Manual Update + Transaction Log is more explicit and robust for "Direct Inbound" feature.
  // The Trigger is good for "PO Workflow" (Order -> Wait -> Receive).
  // Let's do manual update to ensure 'latest_purchase_price' update logic is handled.

  for (const item of items) {
    // A. Insert PO Item
    // We fetch item unit first
    const { data: itemData } = await supabase
      .from("items")
      .select("base_unit, latest_purchase_price")
      .eq("id", item.itemId)
      .single();
      
    if (!itemData) continue;

    await supabase.from("purchase_order_items").insert({
      purchase_order_id: po.id,
      item_id: item.itemId,
      ordered_quantity: item.quantity,
      received_quantity: item.quantity, // Full receive
      unit: itemData.base_unit, // Assume input matches base unit for now
      unit_price: item.unitPrice,
      expiry_date: item.expiryDate
    });

    // B. Update Inventory (Increment)
    // We use the 'deduct_inventory_smart' logic reversed? No, simple increment.
    // But we should use a DB function if possible to ensure consistency.
    // Let's manually update for now as we don't have 'add_inventory' function.
    
    // Check if inventory record exists
    const { data: inv } = await supabase
      .from("inventory")
      .select("id, theoretical_quantity")
      .eq("store_id", storeId)
      .eq("item_id", item.itemId)
      .single();

    if (inv) {
      await supabase
        .from("inventory")
        .update({ 
          theoretical_quantity: inv.theoretical_quantity + item.quantity,
          last_updated_at: new Date().toISOString()
        })
        .eq("id", inv.id);
    } else {
      await supabase
        .from("inventory")
        .insert({
          store_id: storeId,
          item_id: item.itemId,
          theoretical_quantity: item.quantity
        });
    }

    // C. Record Transaction
    await supabase.from("inventory_transactions").insert({
      store_id: storeId,
      item_id: item.itemId,
      transaction_type: "purchase",
      quantity: item.quantity, // Positive for inbound
      unit: itemData.base_unit,
      unit_cost: item.unitPrice,
      total_cost: item.quantity * item.unitPrice,
      reference_type: "purchase_order",
      reference_id: po.id,
      transaction_date: new Date().toISOString(),
      created_by: user.id
    });

    // D. Update Latest Purchase Price if changed
    if (item.unitPrice !== itemData.latest_purchase_price) {
      // Logic: Always update to latest? Or ask user? 
      // Requirement says "consider logic". For MVP "Direct Inbound", updating is standard.
      await supabase
        .from("items")
        .update({ latest_purchase_price: item.unitPrice })
        .eq("id", item.itemId);
    }
  }

  revalidatePath("/dashboard/inventory");
  return { success: true };
}